@import "./include-media";

video {
    align-items: center;
    position: absolute;
    padding: 0;
    margin: 0;
    top: 0;
    left: 0;
    z-index: -1;
    min-height: 100%;
    @include media(">lgdesktop"){
        width: 0;
    }
}

.header-wrapper {
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.3), rgb(0, 0, 0, 1));
    position: relative;
    width: 100%;
    margin-top: 5vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;

    h2 {
        font-size: 1rem;
        text-align: center;
        font-size: 18px;

        @include media(">=phone", "<lgphone") {
            font-size: 14px;
        }

        @include media(">=lgphone", "<tablet") {
            font-size: 16px;
        }

        @include media(">=tablet", "<desktop") {
            font-size: 16px;
        }
    }

    .heading-wrapper {
        h1 {
            font-weight: 200;
            font-size: 6rem;
            text-align: center;
            line-height: 20px;

            @include media(">=phone", "<lgphone") {
                font-size: 1.5rem;
                line-height: 10px;
            }

            @include media(">=lgphone", "<tablet") {
                font-size: 1.5rem;
                line-height: 10px;
            }

            @include media(">=tablet", "<desktop") {
                font-size: 2.5rem;
            }
        }
    }

    p {
        width: 50%;
        text-align: center;
        overflow: hidden;
        width: 100%;

        @include media(">=phone", "<lgphone") {
            font-size: 12px;
            width: 100%;
        }

        @include media(">=lgphone", "<tablet") {
            font-size: 12px;
            width: 100%;
        }

        @include media(">=tablet", "<desktop") {
            font-size: 14px;
            width: 100%;
        }
    }

    .button {
        display: inline-block;
        margin: 15px;
        padding: 15px;
        border: 10px;
        text-decoration: none;
        color: #fff;
        font-size: 1rem;
        letter-spacing: .1rem;
        transition: all .3s;
        position: relative;
        overflow: hidden;
        z-index: 1;

        &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #121212;
            z-index: -2;
        }

        &:before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 0%;
            height: 100%;
            background-color: darken(rgb(105, 105, 105), 15%);
            transition: all .3s;

            z-index: -1;
        }

        &:hover {
            color: #fff;

            &:before {
                width: 100%;
            }
        }
    }
}